<template>
  <div class="container">
    <textinput placeholder="text" type="text" @custominput="data=>{this.text = data}"/>
    {{ text }}
    <checkbox label="gay?" status="false"/>
    <checkbox status="true"/>
    <fileinput usedrag="true"/>
    <p>Девайсы</p>
    <ul>
      <li v-for="(item,index) in devices" :key="index">{{item.deviceId}} {{item.label}}</li>
    </ul>
  </div>
</template>

<script>
import Textinput from "@/components/textinput.component";
import Checkbox from "@/components/checkbox.component";
import Fileinput from "@/components/fileinput.component";

export default {
  name: "home",
  components: {Fileinput, Checkbox, Textinput},
  data() {
    return {
      text: '',
      devices: null
    }
  },
  mounted() {
    setTimeout(()=>{
      navigator.mediaDevices.enumerateDevices().then(devices=>{
        this.devices = devices
      })
    },1000)
  }
}
</script>

<style lang="scss" scoped>
ul{
  display: flex;
  gap: 24px;
  flex-direction: column;
}
</style>
